
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import tools from '../../lib/tools.js';


var host = 'workflow-api.360uyu.com';
if(window.location.host == 'cs.360uyu.com' || window.location.host == 'localhost:3000'){
host = 's-workflow-api.360uyu.com';
}

export default {
name:'myFamilyDoctor',
components:{Flexbox, FlexboxItem,Toast},
data(){
return {
managerId:'',
info:{},
toastMsg:'',
showToast:false,
serviceList:[],
}
},
mounted(){
var vm = this;
let id = (localStorage.getItem('md') || tools.getCookie('user_id'));
if(!id || id=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}// 此时可以跳转到登陆，等登陆后在跳转回来

this.buildType = tools.getRequestParam('buildType');
this.managerId = id;

this.$http.get('/api/contracte/queryDoctors?managerId='+id).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
for(let i=0;i<data.data.length;i++){
if(data.data[i].buildType==vm.buildType){
vm.info = data.data[i];
if(typeof vm.info.createTime === 'string'){
vm.info.createTime = vm.info.createTime.replace(/\-/g, "/");
}
if(typeof vm.info.expirationDate === 'string'){
vm.info.expirationDate = vm.info.expirationDate.replace(/\-/g, "/");
}
vm.info.createTime = tools.simpleDateFormat(vm.info.createTime,'yyyy.MM.dd');
vm.info.expirationDate = tools.simpleDateFormat(vm.info.expirationDate,'yyyy.MM.dd');
this.getServicesByManagerId(vm.managerId,vm.info.managerId);
break;
}
}
}else{


tools.next('/m/familyOrg.html');
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
methods:{
getServicesByManagerId(userId,doctorId){




















var vm = this;
this.$http.post('/service/query?id='+doctorId+'&status=2&pageNumber=1&pageSize=1000&serviceAttribute=1',{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.items){
vm.serviceList = data.items;
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
use(sv){
this.showToast = true;
this.toastMsg = '即将打开对话框...';
var vm = this;
this.$http.axios({method:'post',url:'/procedures/getProcedureDialog?userId='+sv.userId+'&doctorId='+sv.relationSupplierId+'&orderId='+sv.id+'&procedureId='+sv.relationCode}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
console.log(data.data.id);
let targetUrl = "http://"+host+"/v1/procedure-dialog/"+data.data.id;
console.log(targetUrl);
window.location.href=targetUrl;
}else{
vm.toastMsg = data.resultCode.message;
console.error(vm.toastMsg);
}
},(error) => {
vm.toastMsg = '发生错误';
console.error(error);
});
},
goMyFiles(){
if(this.buildType == 3){// 孕产妇
tools.next('/m/gravidaSignEdit.html');
return ;
}
tools.next('/m/familySignUpEdit.html');
},
goTo(id){
tools.next("/m/serviceIndex.html?serviceId="+id);
},
goToFamilyDoctor(){
let vm = this;

this.$http.get('/api/contracte/queryDoctors?managerId='+this.id).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data && data.data.length>0){
tools.next(`/m/myFamilyDoctor.html?managerId=${vm.managerId}&buildType=${vm.buildType}`);

}else{
tools.next('/m/familyOrg.html');
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
}
}



